import { Component } from 'react';
import diyazenView from'./bin/diyazen';
import animateView from'./bin/animate';
import LoadingScreen from './bin/logo/arimac';
require('./App.css');

// get url param
let css_id = window.location.toString();
if(css_id.indexOf('css_id=') > -1) {
	css_id = css_id.split('css_id=')[1]
		.split('#')[0].split('&')[0];
} else { css_id = null; }

class App extends Component {
	render() {
		return (
			<div>
				<div className="diyazen_logo"></div>
				<div className="arimac_load"></div>
				<div className="arimac_logo arimac_logo_white"></div>
			</div>
		);
	};
};

window.addEventListener('load', function() {
	// start loading screen
	LoadingScreen.start('#000');
	// set background animation

	if(css_id === '2') {
		animateView.load();
		diyazenView.load(LoadingScreen.end);
		return;
	}
	if(css_id === null) { css_id = 2; }

	if(css_id !== null) {
		// load css background from url param
		require('./bin/css_animations/background_' + css_id + '.css');
		document.body.className = 'css_background_' + css_id + '_body';
		document.head.className = 'css_background_' + css_id + '_head';
		document.documentElement.className = 'css_background_' + css_id + '_html';
		var dom = document.querySelector('.ani_doms_' + css_id);
		dom.outerHTML = dom.innerHTML;
		// load vignette effect
		require('./bin/css_animations/vignette.css');
		document.querySelector('#vignette').className = 'vignette';
	} else {
		// or load threejs animation
		animateView.load();
	}
	
	// load diyazen model => end loading screen
	diyazenView.load(LoadingScreen.end);
});

var err = [
	'mouseup', 'mousedown', 'mousemove',
	'touchstart', 'touchend', 'touchmove'
];

err.forEach(function(k) {
	window.addEventListener(k, function(e) {
		e.preventDefault(); return false;
	});
});



export default App;