import { TrinityEngine, THREE } from '../engine/4.0/';
import setup from './setup';

let w = window.innerWidth;
let h = window.innerHeight;

let modules = TrinityEngine.createScene(w, h, false);
modules.renderer.domElement.className = 'diyazen-view';

TrinityEngine.setupControls('EXACT', modules.controls,
	{ x : 0, y : 0, z : 0 },
	{ x : -5.214251711247222, y : 2.134722574099269, z : 13.90160201643624 }, {
		autoRotate : false,
		rotateSpeed : 0.5,
		autoRotateSpeed : -1,
		maxPolarAngle : Math.PI / 2.2,
		minPolarAngle : Math.PI / 2.2
	}
);

modules.light.intensity = 0.8;
modules.scene.add(
	TrinityEngine.createLight('POINT', '#FFF', 0.05, { x : +230, y : -4.2, z : 0 }),
	TrinityEngine.createLight('POINT', '#FFF', 0.05, { x : -230, y : -4.2, z : 0 }),
	TrinityEngine.createLight('POINT', '#FFF', 0.2, { x :    0, y : +14.2, z : 4 }),
	TrinityEngine.createLight('POINT', '#FFF', 0.2, { x :    0, y : -14.2, z : 4 }),
	TrinityEngine.createLight('HEMISPHERE', '#fff', 0.2),
	TrinityEngine.createLight('DIRECT', '#fff', 0.2)
);

// ====================================================================================

function loadModel(diyazen, texture) {
	// locate and scale model
	TrinityEngine.setupObject(diyazen, setup.diyazen.transform);
	// setup each material
	Object.values(setup.diyazen.materials).forEach(function(props, i) {
		TrinityEngine.setupMaterial(diyazen.children[4].material[i], props);
	});
	// add to scene
	modules.scene.add(diyazen);
	modules.diyazen = diyazen;

	modules.scene.add(TrinityEngine.createObject({
		Plane : [10, 10],
		MeshBasic : {
			map : texture,
			transparent : true,
			opacity : 0.25
		}
	}, {
		rotation : { x : -Math.PI / 2 },
		position : { y : -4.2 }
	}));
	modules.controls.autoRotate = true;
	setTimeout(loadCallback, 300);
};

function createSpherePoints(head_pos, tail_pos, index, name) {

	// create head point in world
	var head = TrinityEngine.createObject({
		Sphere : [0.05],
		MeshBasic : { color : 'red', transparent : true, opacity : 0 }
	}, { position : head_pos });
	head.name = 'head_' + index;
	modules.scene.add(head);
	window[head.name] = head.position; // for dev

	// create tail point in camera
	var tail = TrinityEngine.createObject({
		Sphere : [0.05],
		MeshBasic : { color : 'white', transparent : true, opacity : 0 }
	}, { position : tail_pos });
	tail.name = 'tail_' + index;
	modules.camera.add(tail);
	window[tail.name] = tail.position; // for dev

	// create line from head and tail
	var points = [
		head.getWorldPosition(new THREE.Vector3()),
		tail.getWorldPosition(new THREE.Vector3())
	];

	var line = new THREE.Line(
		new THREE.BufferGeometry().setFromPoints(points),
		new THREE.LineBasicMaterial({ color: 0xffffff, transparent : true, opacity : 0 })
	);

	line.name = 'line_' + index;
	modules.scene.add(line);

	setup.specs[name].head = head;
	setup.specs[name].tail = tail;
	setup.specs[name].line = line;

	setCardPosition(tail, index, name);
};

function setCardPosition(tail, index, name) {

	let pos = new THREE.Vector3();
	pos = pos.setFromMatrixPosition(tail.matrixWorld);
	pos.project(modules.camera);
	
	let widthHalf = modules.renderer.domElement.width / 2;
	let heightHalf = modules.renderer.domElement.height / 2;
	
	pos.x = (pos.x * widthHalf) + widthHalf;
	pos.y = - (pos.y * heightHalf) + heightHalf;
	pos.z = 0;
	
	var e = document.querySelector('#spec_card_' + index);

	if(setup.specs[name].position.text === 'right') {
		if(window.innerWidth < window.innerHeight) {
			// mobile
			e.style.left = pos.x - 170 + 'px';
		} else {
			// desktop
			e.style.left = pos.x - 200 + 'px';
		}
	} else {
		if(window.innerWidth < window.innerHeight) {
			// mobile
			e.style.left = pos.x + 10 + 'px';
		} else {
			// desktop
			e.style.left = pos.x + 10 + 'px';
		}
	}

	if(window.innerWidth < window.innerHeight) {
		// mobile
		e.style.top  = pos.y + 10 + 'px';
	} else {
		// desktop
		e.style.top  = pos.y - 20 + 'px';
	}

};

function createSpecCards() {
	Object.keys(setup.specs).forEach(function(name, i) {
		var obj = setup.specs[name]
		var pos = obj.position;
		var card_id = 'spec_card_' + i;
		var e = document.querySelector('#' + card_id);
		if(e) {
			// card position on resize
			setCardPosition(obj.tail, i, name);
		} else {
			// create card
			e = document.createElement('div');
			e.className = 'spec_card';
			e.id = card_id; e.style.opacity = 0.2;
			e.style.textAlign = pos.text;
			e.innerHTML += '<div class="spec_card_name">' + name + '</div>';
			e.innerHTML += '<div class="spec_card_info">' + obj.description + '</div>';
			document.querySelector('#root').appendChild(e);
			
			if(window.innerWidth < window.innerHeight) {
				// mobile
				createSpherePoints(pos.head, pos.mobi, i, name);
			} else {
				// desktop
				createSpherePoints(pos.head, pos.tail, i, name);
			}

		}
	});
};

let mDown = false;

window.addEventListener('mousedown', function() { mDown = true; });
window.addEventListener('touchstart', function() { mDown = true; });

window.addEventListener('mouseup', function() { mDown = false; });
window.addEventListener('mouseleave', function() { mDown = false; });
window.addEventListener('mouseout', function() { mDown = false; });
window.addEventListener('touchend', function() { mDown = false; });

function animateSpecCardsAndLines() {
	Object.keys(setup.specs).forEach(function(name, index) {
		var obj = setup.specs[name];
		var card = document.querySelector('#spec_card_' + index);
		// exception
		if(card === null || obj.head === undefined) { return; }

		// check camera range
		var range = obj.position.show;
		var r = modules.camera.rotation.z;
		if(r > range.a && r < range.b && mDown === false) {
			// show card and line
			card.style.opacity = 1;
			TrinityEngine.tweenMaterial('VALUE', obj.line, { opacity : 1 }, 500);
			TrinityEngine.tweenMaterial('VALUE', obj.head, { opacity : 1 }, 500);
			
		} else {
			// hide card and line
			card.style.opacity = 0;
			TrinityEngine.tweenMaterial('VALUE', obj.line, { opacity : 0 }, 200);
			TrinityEngine.tweenMaterial('VALUE', obj.head, { opacity : 0 }, 200);
		}
		
		// update line positions
		var points = [
			obj.head.getWorldPosition(new THREE.Vector3()),
			obj.tail.getWorldPosition(new THREE.Vector3())
		];
		
		obj.line.geometry.dispose();
		obj.line.geometry = new THREE.BufferGeometry().setFromPoints(points);

	});
};

function render() {
	requestAnimationFrame(render);
	modules.renderer.render(modules.scene, modules.camera);
	modules.controls.update();
	animateSpecCardsAndLines();
}; render();

function onResize() {
	w = window.innerWidth;
	h = window.innerHeight;
	TrinityEngine.setupScene(w, h, modules.renderer, modules.camera);
	createSpecCards();
};

TrinityEngine.tweenControls.captureSpot(modules.controls, modules.scene);

window.addEventListener('resize', onResize);

modules.renderer.domElement.addEventListener('click', function(e) {
	//console.log(modules.camera.rotation.z);
});

let diyazenView = {};
let loadCallback = null;

diyazenView['load'] = function(callback) {
	loadCallback = callback;
	document.querySelector('#root').appendChild(modules.renderer.domElement);
	createSpecCards();
	onResize();

	let files = {
		model : setup.diyazen.source,
		floor : 'textures/ground_shadow.png'
	}

	TrinityEngine.loadAll(files, function(files) {
		loadModel(files.model, files.floor);
	});

};

export default diyazenView;