import { TrinityEngine, THREE } from '../engine/4.0/';

let modules = TrinityEngine.createScene(false);

modules.renderer.domElement.className = 'animate-view';

TrinityEngine.setupControls('EXACT', modules.controls,
	{ x : 0, y : 0, z :  0 },
	{ x : 0, y : 0, z : 20 },
    { enabled : false }
);

modules.light.visible = false;

let boxSpace = { x : 28, y : 15 };
let point, card, boxes, wall, light;

function init() {

    // POINT ===================================================================

    point = TrinityEngine.createObject({
        Box : [1, 1, 1],
        MeshStandard : { color : 'red' },
    }, {
        visible : false,
        position : { x : 30, y : 0, z : -20 }
    });

    modules.scene.add(point);

    // BOXES ===================================================================

    if(window.innerWidth < window.innerHeight) {
        boxSpace = { x : 6, y : 10 };
    }

    boxes = TrinityEngine.createParticles(
        'XYZ', 'XYZ', -boxSpace.x, boxSpace.x, -boxSpace.y, boxSpace.y, -7, -5, 2, 2, 2,
        /*TrinityEngine.createObject({
            Box : [0.6, 0.6, 0.6],
            MeshStandard : {
                color : '#3366ff',
                transparent : true,
                opacity : 0.2
            }
        }),*/
        TrinityEngine.createObject({
            Sphere : [0.2],
            MeshStandard : {
                color : '#3366ff',
                transparent : true,
                opacity : 0.2
            }
        })
    );

    boxes.forEach(function(e) { modules.scene.add(e); })

    // CARD ===================================================================

    wall = TrinityEngine.createObject({
        Box : [56, 30, 0.2],
        MeshStandard : { color : '#111' },
    }, {
        position : { x : 0, y : 0, z : -8 },
        transparent : true, opacity : 0
    });

    modules.scene.add(wall);

    card = TrinityEngine.createObject({
        Box : [56, 30, 0.2],
        MeshStandard : { color : '#111' },
    }, {
        position : { x : 0, y : 0, z : -3 },
        transparent : true, opacity : 0,
        visible : false
    });

    modules.scene.add(card);

    // LIGHT ===================================================================

    light = new THREE.SpotLight('#3366ff');

    light.intensity = 2;
    light.distance = 80;
    light.decay = 12;
    light.position.set(0, 0, -1)
    light.angle = 4;

    modules.scene.add(light);
    light.target = point;



    TrinityEngine.createEvent('mousemove', card, function(e) {
        point.position.set(
            e.point.x,
            e.point.y,
            point.position.z,
        );
        light.target = point;
    });

    
    setInterval(animateBoxes, 500);

};

// BLOOM ===================================================================



let effect = TrinityEngine.createEffect(
    'BLOOM', modules.renderer, modules.scene, modules.camera
);

function animateBoxes() {
    boxes.forEach(function(box) {
        var x = box.position.x + (0.4 * Math.random());
        var y = box.position.y + (0.4 * Math.random());
        if(x > boxSpace.x || y > boxSpace.y) {
            box.position.set(
                -box.position.x,
                -box.position.y,
                box.position.z
            );
        }
        else {
            TrinityEngine.tweenObject(box, {
                position : { x : x, y : y }
            }, 500, 'power0');
        }
    });
};


function animate() {
    requestAnimationFrame(animate);
    //modules.renderer.render(modules.scene, modules.camera);
    effect.composer.render();
}; animate();

function onResize() {
	let w = window.innerWidth;
	let h = window.innerHeight;
	TrinityEngine.setupScene(w, h, modules.renderer, modules.camera, effect.composer);
};

window.addEventListener('resize', onResize);

let animateView = {};

animateView.load = function() {
    init();
    document.querySelector('#root').appendChild(modules.renderer.domElement);
};

export default animateView;